@import "../../../../assets/style/mixins.scss";

.main {
  @include reset(li);
  width: 100%;
  max-width: 795px;
}

.item {
  max-width: inherit;
}

.desc {
  margin: 0 0 20px 0;
  line-height: 130%;
  @media (max-width: 600px) {
    text-align: center;
  }
  @media (max-width: 450px) {
    margin: 0 0 15px 0;
  }
}

.label {
  position: absolute;
  overflow: hidden;
  opacity: 0;
  width: 0;
  height: 0;
  font-size: 0;
}

.input {
  width: 100%;
  margin-bottom: 15px;
  @include border(2px, 3px);
  padding: 5px 15px;
  @include transition(background-color);
  &:not(input) {
    min-width: 280px;
    max-width: inherit;
    min-height: 200px;
    max-height: 400px;
  }
  @media (max-width: 600px) {
    padding: 5px 10px;
  }
  @media (max-width: 450px) {
    padding: 5px;
    margin-bottom: 10px;
    &:not(input) {
      min-width: 260px;
      min-height: 100px;
      max-height: 200px;
    }
  }
}
