@import "../../../assets/style/mixins.scss";

.form,
.container {
  display: flex;
}

.blockquote__box {
  margin-bottom: 50px;
  @media (max-width: 450px) {
    margin-bottom: 20px;
  }
}

.container {
  @include position(relative, 4);
  height: max-content;
  @media (max-width: 1146px) {
    flex-direction: column;
  }
}

.form {
  @include us-title-box();
  flex-direction: column;
  justify-content: space-between;
  margin-right: 30px;
  @include visible-translate-X(-500px);
  &__title {
    @include us-title();
  }
  @media (max-width: 1146px) {
    margin: 0 0 20px 0;
  }
}

.map__box {
  overflow: hidden;
  height: 600px;
  width: 100%;
  border-radius: 3px;
  @include visible-translate-X(500px);
  @media (max-width: 800px) {
    height: 400px;
  }
  @media (max-width: 450px) {
    height: 300px;
  }
}
