@import "../../../assets/style/mixins.scss";

.main {
  margin: 0 0 10px 0;
  line-height: 120%;
}

.box {
  @include position(relative, 10);
  margin: 0 0 30px 0;
  @include visible-translate-X(-100px);
  @media (max-width: 450px) {
    margin: 0 0 25px 0;
  }
}

.author {
  padding-left: 15px;
  @media (max-width: 450px) {
    padding-left: 0;
  }
}

.box,
.author {
  @media (max-width: 450px) {
    text-align: center;
  }
}
