@import "../../../../assets/style/mixins.scss";

.main {
  display: flex;
  align-items: center;
  height: 55px;
  margin-bottom: 15px;
  padding: 5px 20px;
  font-size: 1.2rem;
  font-weight: 700;
  @include transition(background-color);
  & svg {
    margin-right: 10px;
    @include block-size(100%, calc(100% / 1.5));
  }
  @media (max-width: 450px) {
    height: 35px;
    margin-bottom: 10px;
  }
}

.message {
  overflow: hidden;
  width: 100%;
  max-height: 0;
  margin: 0;
  font-size: 0.8rem;
  font-weight: 600;
  transition: max-height 0.4s ease-in-out;
  &__active {
    max-height: 1000px !important;
    transition: max-height 0.4s ease-in-out !important;
  }
}
