@import "../../../../assets/style/mixins.scss";

.container,
.contact__list,
.social__list {
  @include reset(li);
  display: flex;
}

.container {
  padding-bottom: 40px;
  @media (max-width: 1146px) {
    flex-wrap: wrap;
  }
  @media (max-width: 450px) {
    padding-bottom: 20px;
  }
}

.contact,
.social {
  &__title {
    @include us-title();
  }
  &__box {
    @include us-title-box();
    width: 100%;
    &:not(:last-child) {
      margin-right: 30px;
    }
  }
  @media (max-width: 1146px) {
    &__box {
      &:not(:last-child) {
        margin: 0 0 40px 0;
      }
    }
  }
  @media (max-width: 450px) {
    &__box {
      &:not(:last-child) {
        margin: 0 0 20px 0;
      }
    }
  }
}

.contact {
  &__box {
    @include visible-translate-X(-500px);
  }
  &__list {
    flex-direction: column;
  }
  &__item {
    user-select: text;
    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }
  @media (max-width: 450px) {
    &__item {
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }
}

.social {
  &__box {
    display: flex;
    align-items: center;
    @include visible-translate-X(500px);
  }
  &__list {
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 100%;
  }
  &__item {
    margin: 15px;
    &:not(:last-child) {
      margin-right: 15px;
    }
  }
  @media (max-width: 800px) {
    &__item {
      margin: 7px 0;
      &:not(:last-child) {
        margin-right: 10px;
      }
    }
  }
  @media (max-width: 450px) {
    &__item {
      margin: 5px 0;
      &:not(:last-child) {
        margin-right: 5px;
      }
    }
  }
}
